import Modal from 'react-bootstrap/Modal';
import Carousel from 'react-bootstrap/Carousel';
import Button from 'react-bootstrap/Button';


const WorkModal = ({ itemId, onClose, workData }) => {
    const item = workData.find(item => item.id === itemId);
    console.log(item.skills); // This will log to console whenever the modal is rendered with an item

    return (
        <Modal show={!!item} onHide={onClose} centered>
            <Modal.Header closeButton>
                <Modal.Title>
                    <div className='w-100 d-flex flex-row justify-content-between align-items-center'>
                        {item ? item.title : 'Loading...'}
                        {item && item.skills && item.skills.length > 0 && (
                            item.skills.map((skill, index) => (
                                <div className="skill-item" key={index}>
                                    <Button variant="secondary m-2" className='p-1 ms-5'>
                                        <i className={skill.icon || 'fa fa-question-circle'}></i>  {skill.name}
                                    </Button>
                                </div>
                            ))
                        )}
                    </div>
                </Modal.Title>
            </Modal.Header>
            <Modal.Body>
                {item && (
                    <Carousel interval={3000} fade>
                        {item.images && item.images.length > 0 ? (
                            item.images.map((img, index) => (
                                <Carousel.Item key={index}>
                                    <img className="d-block w-100" src={img} alt={`Slide ${index + 1}`} />
                                </Carousel.Item>
                            ))
                        ) : (
                            <Carousel.Item>
                                <img className="d-block w-100" src={item.img} alt={item.title} />
                            </Carousel.Item>
                        )}
                    </Carousel>
                )}
                <p>{item ? item.description : ''}</p>
                <div className='d-flex'>
                    <Button variant="secondary m-2" href={item.url} target="blank"><i className="fa-solid fa-globe" ></i>  Visitez le site</Button>
                    <Button variant="secondary m-2" href={item.gitHub} target="blank"><i className="fa-solid fa-globe" ></i>  Repo Github</Button>

                </div>
            </Modal.Body>
        </Modal>
    );
};

export default WorkModal;
